.Box {
    border : 4px solid black;
    text-align: center;
    border-radius: 5px;
    width : 600px;
    margin : auto;
    background-color: blanchedalmond;
}

.Text {
    font-size : 15px;
    font-weight : bold;
    border: 3px solid coral;
    border-radius: 7px;
    margin : 10px auto;
    width : 400px;
    padding : 10px;
    background-color: coral;
    color:aliceblue
}

.logo {
    width: 150px;
    height: 50px;
    object-fit: cover;
    float: right;
}

.nft-img {
    width:300px;
    height:300px;
    object-fit: contain;
    border : 3px solid black;
    margin : 5px;
    border-radius : 7px;
}

.msg {
    font-size: 17px;
    font-weight: bold;
    border: 3px solid skyblue;
    border-radius: 7px;
    margin : 10px auto;
    width: 350px;
    background-color: aqua;
}

.back {
    display: inline-block;
    font-size: 40px;
    text-decoration-line: none;
    height: 50px;   
}

.Title {
    font-size: 40px;
    font-weight: bold;
    text-align: center;
}