.nft-form {
  border: 2px solid black;
  width: 50%;
  display: flex;
  justify-content: center;
}

.container {
  display: flex;
  justify-content: center;
}

.title {
  padding: 10px;
  margin: 0;
}

.key {
  padding: 5px;
  padding-right: 20px;
}
.value {
  padding: 5px;
  padding-left: 10px;
}

.box {
  padding: 10px;
  display: flex;
  align-items: center;
}

