#mint-button:active,
#mint-button:hover {
  outline: 0;
}

@media (min-width: 768px) {
  #mint-button {
    font-size: 24px;
    min-width: 196px;
  }
}

#mint-button {
  background-image: linear-gradient(to right, #6c927b -3%, #f2cec6);
}
