.user-info-container {
  @apply flex items-center  flex-col w-full;
}

.user-info-title {
  @apply lg:text-[32px] text-[24px] text-mono100 text-center;
}

.user-wallet-info {
  @apply border-brown100 border-[3px] w-full rounded-2xl flex items-center justify-center lg:p-12 p-6 bg-white;
}

.wallet-info-content h3 {
  @apply m-3;
}

.metamask-button-list {
  @apply flex flex-col space-y-10 mt-10 lg:max-w-[520px] w-full max-w-[300px] mb-[80px];
}

.metamask-button-list button {
  @apply w-full flex items-center justify-center shadow-lg hover:bg-pink100 hover:text-cream100 hover:border-green60 rounded-2xl p-3 text-[24px] text-cream100 font-semibold bg-green60;
}

/* 1024px 이상의 화면 크기에 대한 스타일 (배경 이미지 포함) */
@media screen and (min-width: 1024px) {
  .metamask-button-list button {
    background-image: linear-gradient(to right, #679277 -5%, #f0c7bd);
  }
}

.metamask-button-list span {
  @apply ml-2;
}

