/* CSS를 잘 몰라서 기본적인 처리만 했기 때문에, 얼마든지 수정하셔도 됩니다! */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* 모든 css파일은 App.css에서 import시킵니다. */
/* @import "./layout.css"; */

@layer base {
  * {
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
  }

  html,
  body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: "NanumSquareNeo-Variable", sans-serif;
  }
}

/* 모달 */
.modal-container {
  @apply border border-black flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.2);
}

.modal-content {
  @apply flex rounded-lg text-brown100 text-center lg:text-[20px] text-[12px] justify-center lg:w-[35rem] w-[70%];
  padding: 3rem;
  background-color: #fff;
  position: relative;
}
